import React from 'react'
import {Container,Row,Button} from 'react-bootstrap';
import Product from '../components/Product';
import Title from '../components/Title';
import { useState } from 'react';
import data2 from '../data2';
import Nike from '../components/Nike';
import axios from 'axios'

const Home = (props) => {
console.log(props)
    const {shoes,setShoes,no,setNo}=props
    const [nike, setnike] =useState(data2)
    const [count, setCount]=useState(1)
    const title =[{title_h3 :`MD'S PICK`,tit_p :'  ,  .'},{tit_h3 :'   ', tit_p:'      ' }]
    return (
        <>
            <div className="bg"></div>
            {/*section1*/}
            <Title title={title[0]} />
            <Button variant="outline-primary" onClick={() => {
                let copy = [...shoes].sort((a, b) =>
                    (a.title > b.title ? 1 : -1))
                setShoes(copy)

                let no_copy = []
                for (let i in copy) {
                    no_copy.push(copy[i].id)
                }
                setNo(no_copy)

            }}> </Button>{' '}
            <Button variant="outline-secondary" onClick={() => {
                let copy = [...shoes].sort((a, b) =>
                    (a.price > b.price) ? 1 : -1
                )
                setShoes(copy)
            }}>  </Button>{' '}

            <Button variant="outline-success" onClick={() => {
                let copy = [...shoes].sort((a, b) =>
                    (a.price < b.price) ? 1 : -1
                )
                setShoes(copy)
            }}> </Button>
            <Container>

                <Row>
                    {
                        shoes.map((value, i) =>
                            <Product shoes={value} key={i} i={i} no={no} />

                        )
                    }
                </Row>
            </Container>
            {/*section2*/}
            <Title title={title[1]} />
            <Button variant="outline-secondary" onClick={()=>{
              if(count ===1){
         
                axios.get('https://younga298.github.io/react_data/nike2.json')
                .then((result)=>{
                    let copy = [...nike, ...result.data]
                    setnike(copy)
                    setCount(2)
                })
              }else if(count ===2){
                axios.get('https://younga298.github.io/react_data/nike3.json')
                .then((result)=>{
                    let copy = [...nike, ...result.data]
                    setnike(copy)
                    setCount(3)
                })
                
              }else {
                alert('   ')
              }
            }}>+3  </Button>
            <Container style={{marginTop:'30px'}}>

                <Row>
                    {
                        nike.map((value, i) =>
                            <Nike nike={value} key={i}/>

                        )
                    }
                </Row>
            </Container>
        </>
    )
}

export default Home